.user-wrapper{
  background-color: #eaeaea;
  padding: 10px;
  border-radius: 15px;
  margin: 10px auto;
  display: flex;
  max-width: 400px;
  column-gap: 30px;
  align-items: center;
}
.App{
  max-width: 470px;
  padding: 15px;
  border-radius: 15px;
  margin: 30px auto;
  background-color: #353535;
}
.avatar{
  border-radius: 50%;
  width: 80px;
}
.user-email{
  color: #353535;
}
p{
  margin: 2px auto;
}
.pagination { 
  margin: 30px auto;
  text-align: center;
}
.pagination > button {
  padding: 5px ;
  cursor: pointer;
  margin: 5px;
  width: 40px;
  height: 40px;

}
.active{
  background: #400264;
  color: #fff;
  border-radius: 10px;
  border: none;
  width: 40px;
  height: 40px;
  padding: 10px;
}
.loader{
  text-align: center;
}